import Carousel from "react-bootstrap/Carousel";

import imgModelling1 from "../assets/img/blender/modelling_img_1.jpg";
import imgModelling2 from "../assets/img/blender/modelling_img_2.jpg";
import imgModelling3 from '../assets/img/blender/modelling_img_3.jpg';

const ModellingCarousel = () => {
  return (
    <Carousel>
      <Carousel.Item>
        <img src={imgModelling1} alt="First art piece" />
      </Carousel.Item>
      <Carousel.Item>
        <img src={imgModelling2} alt="Second art piece" />
      </Carousel.Item>
      <Carousel.Item>
        <img src={imgModelling3} alt="Third art piece" />
      </Carousel.Item>
    </Carousel>
  );
};

export default ModellingCarousel;
