import Carousel from "react-bootstrap/Carousel";

import imgRochefort1 from "../assets/img/rochefort/studio_1.jpg";
import imgRochefort2 from "../assets/img/rochefort/studio_2.jpg";
import imgRochefort3 from "../assets/img/rochefort/studio_3.jpg";
import imgRochefort4 from "../assets/img/rochefort/studio_4.jpg";
import imgRochefort5 from "../assets/img/rochefort/studio_5.jpg";
import imgRochefort6 from "../assets/img/rochefort/studio_6.jpg";

const RochefortCarousel = () => {
  return (
    <Carousel>
      <Carousel.Item>
        <img src={imgRochefort1} alt="Rochefort Studio 1" />
      </Carousel.Item>
      <Carousel.Item>
        <img src={imgRochefort2} alt="Rochefort Studio 2" />
      </Carousel.Item>
      <Carousel.Item>
        <img src={imgRochefort3} alt="Rochefort Studio 3" />
      </Carousel.Item>
      <Carousel.Item>
        <img src={imgRochefort4} alt="Rochefort Studio 4" />
      </Carousel.Item>
      <Carousel.Item>
        <img src={imgRochefort5} alt="Rochefort Studio 5" />
      </Carousel.Item>
      <Carousel.Item>
        <img src={imgRochefort6} alt="Rochefort Studio 6" />
      </Carousel.Item>
    </Carousel>
  );
};

export default RochefortCarousel;
