import React, { useState } from "react";

import { Icon } from "@iconify/react";
import menuOutlined from "@iconify/icons-ant-design/menu-outlined";

const Nav = () => {
  const [displayNav, setDisplayNav] = useState(false);

  // Handles navigation display on mobile devices
  const handleNavDisplay = () => {
    setDisplayNav((prevState) => !prevState);
  };

  return (
    <nav id="nav-container">
      <div id="nav-icon"></div>

      <ul id="nav-links">
        <li id="nav-home" className="nav-li-group">
          <a href="#home">
            Home
          </a>
        </li>
        <li id="nav-projects" className="nav-li-group">
          <a href="#projects-nav">
            Projects
          </a>
        </li>
        <li id="nav-others" className="nav-li-group">
          <a href="#others-nav">
            Others
          </a>
        </li>
      </ul>

      <div id="nav-hamburger" onClick={() => handleNavDisplay()}>
        <Icon icon={menuOutlined} />
      </div>

      {displayNav ? (
        <ul id="nav-links-mobile">
          <li>
            <a href="#home" onClick={() => handleNavDisplay()}>
              Home
            </a>
          </li>
          <li>
            <a href="#projects-nav" onClick={() => handleNavDisplay()}>
              Projects
            </a>
          </li>
          <li>
            <a href="#others-nav" onClick={() => handleNavDisplay()}>
              Others
            </a>
          </li>
        </ul>
      ) : (
        ""
      )}
    </nav>
  );
};

export default Nav;
