const Project = ({ project, nextContent, prevContent }) => {
  return (
    <>
      <h3 id="project-name">
        <em>{project.name}</em>
      </h3>

      <div id="project-content">
        <a className="prev" onClick={() => nextContent()}></a>
        <p id="project-description">
          {project.description}
        </p>
        <a className="next" onClick={() => prevContent()}></a>
      </div>

      <a id="project-button" href={project.link} target="_blank" rel="noopener noreferrer">See Project</a>
    </>
  );
};

export default Project;
