import { Icon } from "@iconify/react";
import codepenCircleFilled from "@iconify/icons-ant-design/codepen-circle-filled";
import githubFilled from "@iconify/icons-ant-design/github-filled";
import linkedinWithCircle from "@iconify/icons-entypo-social/linkedin-with-circle";

const Footer = () => {
  return (
    <footer id="footer">
      <h2 id="footer-title">Links</h2>

      <ul id="footer-links">
        <li>
          <a
            id="footer-github"
            href="https://github.com/DerianMelvin"
            target="_blank"
            rel="noopener noreferrer"
          >
            <Icon icon={githubFilled} className="footer-icon" />
          </a>
        </li>
        <li>
          <a
            id="footer-codepen"
            href="https://codepen.io/derianmelvin"
            target="_blank"
            rel="noopener noreferrer"
          >
            <Icon icon={codepenCircleFilled} className="footer-icon" />
          </a>
        </li>
        <li>
          <a
            id="footer-linkedin"
            href="https://www.linkedin.com/in/derian-melvin-halim-0704a81b7/"
            target="_blank"
            rel="noopener noreferrer"
          >
            <Icon icon={linkedinWithCircle} className="footer-icon" />
          </a>
        </li>
      </ul>
    </footer>
  );
};

export default Footer;
