import React, { useState } from "react";
import Project from "./Project";

import imgMarkdownPreviewer from '../assets/img/projects/markdown_previewer.jpg';
import imgEventTracker from '../assets/img/projects/event_tracker.jpg';
import imgTechnicalDocumentation from '../assets/img/projects/technical_documentation_page.jpg';

const Projects = () => {
  const [project, setProject] = useState([
    {
      id: 1,
      name: "Markdown Previewer",
      description:
        "Formats plain text into a more appealing and user-friendly reading experience using Markdown.",
      link: "https://codepen.io/derianmelvin/pen/abmxZmY",
      image: imgMarkdownPreviewer,
      alt: "markdown previewer",
    },
    {
      id: 2,
      name: "Event Tracker",
      description:
        "Tracks occurring natural events through NASA's EONET API and displays available information of the event.",
      link: "https://github.com/DerianMelvin/nasa-event-tracker",
      image: imgEventTracker,
      alt: "event tracker",
    },
    {
      id: 3,
      name: "Technical Documentation",
      description:
        "View and navigate between sections of documentation details using pure HTML and CSS.",
      link: "https://codepen.io/derianmelvin/pen/LYZoMVj",
      image: imgTechnicalDocumentation,
      alt: "technical documentation",
    },
  ]);
  const [currentProject, setCurrentProject] = useState(0);

  const nextContent = () => {
    if (currentProject < project.length - 1) {
      setCurrentProject((prevState) => prevState + 1);
    } else {
      setCurrentProject(0);
    }
  };

  const prevContent = () => {
    if (currentProject > 0) {
      setCurrentProject((prevState) => prevState - 1);
    } else {
      setCurrentProject(project.length - 1);
    }
  };

  return (
    <main id="projects">
      <div id="projects-header-container">
        <div id="projects-nav"></div>
        <h2 id="projects-title">Projects</h2>
      </div>

      <div id="projects-content-container">
        <div id="projects-project-info">
          <Project
            project={project[currentProject]}
            nextContent={nextContent}
            prevContent={prevContent}
          />
        </div>

        <div id="projects-project-image">
          <img src={project[currentProject].image} alt={project[currentProject].alt} />
        </div>
      </div>
    </main>
  );
};

export default Projects;
