import ModellingCarousel from "./ModellingCarousel"
import RochefortCarousel from "./RochefortCarousel"

const Others = () => {
  return (
    <div id="others">
      <div id="others-header-container">
        <div id="others-nav"></div>
        <h2 id="others-title">Others</h2>
      </div>

      <div id="others-content-container">
        <div id="modelling">
          <h3 id="modelling-title">3D Modelling</h3>
          <div id="modelling-carousel">
            <ModellingCarousel />
          </div>
          <a href="https://www.instagram.com/diremlavn/" target="_blank" rel="noopener noreferrer">
            @diremlavn
          </a>
        </div>

        <div id="rochefort-studio">
          <h3 id="rochefort-title">Rochefort Studio</h3>
          <div id="rochefort-carousel">
            <RochefortCarousel />
          </div>
        </div>
      </div>
    </div>
  );
};

export default Others;
